:root {
  --rotation-deg: calc(360 * 23 / 60);
}

body {
  max-width: 1600px;
  background-image: url("background.59494db8.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

textarea {
  resize: none;
}

.mechanical-clock {
  width: 500px;
  height: 500px;
  background-image: url("clock.8bc569ff.png");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  position: relative;
  left: 600px;
}

.clock-arrow {
  transform-origin: 0 100%;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock-hours__arrow {
  width: 10px;
  height: 25%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 7px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-seconds__arrow {
  width: 5px;
  height: 40%;
  bottom: 50%;
  left: 50%;
}

